/*Btn Btn-Primary*/
.btn {
    padding: 10px 30px;
    border-radius: 30px;
    transition: $transition;

    font: {
        size: 16px;
        weight: 500;
    }

    ;
}

.btn-primary {
    color: $white_color;
    background-color: $template_color;
    border-color: $template_color;

    &:hover,
    &:focus,
    &.disabled:hover,
    &.disabled:focus {
        color: $template_color;
        background-color: $white_color;
        border-color: $template_color;
    }

    &:not(:disabled):not(.disabled).active,
    &:not(:disabled):not(.disabled):active {
        color: $white_color;
        background-color: $template_color;
        border-color: $template_color;
    }

    &:not(:disabled):not(.disabled).active:focus,
    &:not(:disabled):not(.disabled):active:focus {
        box-shadow: none;
    }

    &.focus,
    &:focus {
        box-shadow: none;
    }

    &.disabled,
    &:disabled {
        color: $white_color;
        background-color: $template_color;
        border-color: $template_color;
        opacity: 1;
    }
}

/* Section Title */
.section-title {
    margin-bottom: 60px;
    padding-left: 30px;
    position: relative;

    &::before,
    &::after {
        content: '';
        position: absolute;
        width: 3px;
        height: 100%;
        left: 10px;
        top: 0;
        background: $template_color;
        transition: $transition;
    }

    &::after {
        left: 0;
        height: 50%;
    }

    &:hover::before {
        height: 50%;
    }

    &:hover::after {
        height: 100%;
    }

    h2 {
        margin-bottom: 20px;

        font: {
            size: 40px;
            weight: 400;
        }

        span {
            color: $template_color;
            font-weight: 600;
        }
    }

    p {
        max-width: 635px;
        margin-top: -7px;
    }

    h4 {
        color: $template_color;
        margin-bottom: 14px;
        text-transform: capitalize;

        font: {
            size: 17px;
            weight: 400;
        }
    }
}

/* Form Control */
.form-control {
    height: 45px;
    padding: 10px;
    font-size: 15px;
    background-color: $white_color;
    border: 1px solid #eeeeee;
    border-radius: 0;

    &:focus {
        border-color: #dddddd;
        outline: 0;
        box-shadow: none;
    }
}

/* Owl Carousel Button */
.owl-theme {
    .owl-dots {
        .owl-dot {
            span {
                width: 15px;
                height: 15px;
                margin: 0 4px;
                background: transparent;
                border-radius: 50%;
                border: 2px solid #dddddd;
                transition: $transition;
            }

            &.active {
                span {
                    background: #869791;
                    border-radius: 5px;
                    width: 35px;
                    height: 10px;
                    border-color: $template_color;
                    background: $template_color;
                }
            }

            &:hover {
                span {
                    background: $template_color;
                    border-color: $template_color;
                }
            }
        }
    }
}

/*==============================
Preloader CSS
==============================*/
.preloader {
    position: fixed;
    width: 100%;
    height: 100%;
    background: $white_color;
    z-index: 99999;

    .folding-cube {
        left: 0;
        width: 60px;
        height: 60px;
        position: absolute;
        transform: rotateZ(45deg);
        top: 50%;
        right: 0;
        margin: -60px auto 0;

        .cube {
            float: left;
            width: 50%;
            height: 50%;
            position: relative;
            transform: scale(1.1);

            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: $template_color;
                animation: sk-foldCubeAngle 2.4s infinite linear both;
                transform-origin: 100% 100%;
            }
        }

        .cube2 {
            transform: scale(1.1) rotateZ(90deg);

            &::before {
                animation-delay: 0.3s;
            }
        }

        .cube3 {
            transform: scale(1.1) rotateZ(180deg);

            &::before {
                animation-delay: 0.6s;
            }
        }

        .cube4 {
            transform: scale(1.1) rotateZ(270deg);

            &::before {
                animation-delay: 0.9s;
            }
        }
    }
}

@keyframes sk-foldCubeAngle {

    0%,
    10% {
        transform: perspective(140px) rotateX(-180deg);
        opacity: 0;
    }

    25%,
    75% {
        transform: perspective(140px) rotateX(0deg);
        opacity: 1;
    }

    90%,
    100% {
        transform: perspective(140px) rotateY(180deg);
        opacity: 0;
    }
}

/*==============================
Main Header CSS
==============================*/
.navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 2;
    background: transparent !important;

    padding: {
        top: 30px;
        bottom: 30px;
        left: 15px;
        right: 15px;
    }

    ;
}

.header-sticky {
    &.is-sticky {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 999;
        box-shadow: 0 2px 28px 0 rgba(0, 0, 0, 0.06);
        animation: 500ms ease-in-out 0s normal none 1 running fadeInDown;
        background: $white_color !important;

        padding: {
            top: 20px;
            bottom: 20px;
        }

        ;
    }
}

.navbar-light {
    .navbar-brand {
        color: $white_color;
        text-transform: uppercase;
        line-height: 10px;
        transition: $transition;

        font: {
            size: 23px;
            weight: 600;
        }

        span {
            color: $template_color;
            line-height: 10px;
            text-transform: lowercase;
            transition: $transition;

            font: {
                size: 40px;
                weight: 700;
            }
        }

        &:focus,
        &:hover {
            color: $white_color;
        }
    }

    .navbar-nav {
        .nav-item {
            padding: {
                right: 15px;
                left: 15px;
                top: 0;
                bottom: 0;
            }

            ;

            &:last-child {
                padding-right: 0;
            }

            &:first-child {
                padding-left: 0;
            }

            .nav-link {
                color: $white_color;
                position: relative;
                font-weight: 500;
                transition: $transition;

                padding: {
                    right: 0;
                    left: 0;
                    top: 0;
                    bottom: 0;
                }

                ;

                &::before {
                    content: '';
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    width: 100%;
                    height: 2px;
                    background-color: $template_color;
                    transition: $transition;
                    transform: scaleX(0);
                }

                &.active,
                &.show,
                &:hover,
                &:focus {
                    color: $template_color;

                    &::before {
                        transform: scaleX(1);
                    }
                }
            }
        }

        .active,
        &.show {
            .nav-link {
                color: $template_color;
            }
        }
    }

    &.header-sticky {
        &.is-sticky {
            .navbar-brand {
                color: $black_color;
            }

            .navbar-nav {
                .nav-link {
                    color: $black_color;

                    &.active,
                    &.show,
                    &:hover,
                    &:focus {
                        color: $template_color;
                    }
                }

                .active,
                .show {
                    .nav-link {
                        color: $template_color;
                    }
                }
            }
        }
    }
}

// Mobile and iPad Navbar
@media only screen and (max-width: 991px) {

    .navbar {
        padding: {
            top: 15px;
            bottom: 15px;
        }

        ;
    }

    .navbar-light {
        .navbar-toggler {
            border-color: $white_color;
            border-radius: 0;

            .navbar-toggler-icon {
                background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(255, 255, 255, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
            }
        }

        .navbar-nav {
            background-color: $white_color;
            padding: 10px 20px;
            margin-top: 10px;

            .nav-item {
                padding: {
                    left: 0;
                    right: 0;
                    top: 10px;
                    bottom: 10px;
                }

                ;

                .nav-link {
                    color: $black_color;
                    display: inline-block;

                    &.active,
                    &:hover {
                        color: $template_color;
                    }
                }
            }
        }

        &.header-sticky {
            &.is-sticky {
                padding: {
                    top: 15px;
                    bottom: 15px;
                }

                ;

                .navbar-toggler {
                    border-color: $black_color;

                    .navbar-toggler-icon {
                        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(0, 0, 0, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
                    }
                }

                .navbar-nav {
                    border: 1px solid #eeeeee;
                }
            }
        }
    }

}

