/*==============================
Creative Banner CSS
==============================*/
.creative-banner-one {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    border-width: 10rem 100vw 0 0;
    border-color: transparent $white_color $white_color transparent;
    border-style: solid;
}

.creative-banner-two {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;

    &::before {
        content: "";
        border-right: 50vw solid $white_color;
        border-top: 160px solid transparent;
        bottom: 0;
        right: 0;
        position: absolute;
        height: 0;
        width: 0;
    }

    &::after {
        content: '';
        position: absolute;
        border-left: 50vw solid $white_color;
        border-top: 160px solid transparent;
        bottom: 0;
        height: 0;
        left: 0;
        width: 0;
    }
}

.creative-banner-three {
    position: absolute;
    right: 0;
    left: 0;
    bottom: 0;

    &::before {
        content: '';
        position: absolute;
        bottom: -25px;
        left: 0;
        width: 100%;
        height: 150px;

        background: {
            image: url(../../assets/img/shape.png);
            position: center center;
            size: cover;
            repeat: no-repeat;
        }

        ;
    }
}

