.about-video {
    position: relative;

    .video-btn {
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        text-align: center;
        transform: translateY(-50%);

        a {
            position: relative;
            display: inline-block;
            width: 90px;
            height: 90px;
            line-height: 92px;
            background-color: $white_color;
            border-radius: 50%;
            color: $template_color;
            z-index: 1;
            font-size: 35px;

            &::after,
            &::before {
                content: '';
                display: block;
                position: absolute;
                top: 0;
                right: 0;
                z-index: -1;
                bottom: 0;
                left: 0;
                border-radius: 50%;
                border: 1px solid $white_color;
            }

            &::before {
                animation: ripple 2s linear infinite
            }

            &::after {
                animation: ripple 2s linear 1s infinite
            }

            &:hover,
            &:focus {
                background-color: $template_color;
                color: $white_color;
            }
        }
    }
}

@keyframes pulse-border {
    0% {
        transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
        opacity: 1;
    }

    100% {
        transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.2);
        opacity: 0;
    }
}

@keyframes ripple {
    0% {
        transform: scale(1)
    }

    75% {
        transform: scale(1.75);
        opacity: 1
    }

    100% {
        transform: scale(2);
        opacity: 0
    }
}

