.who-we-are-area {
    position: relative;
    z-index: 1;

    background: {
        image: url(../../assets/img/bg2.jpg);
        position: center center;
    }

    ;
}

.single-who-we-are {
    margin-bottom: 30px;
    position: relative;
    z-index: 1;
    background: $white_color;
    border: 1.5px dashed #eeeeee;
    padding: 30px;
    transition: $transition;

    i {
        font-size: 50px;
        color: $template_color;
        transition: $transition;
    }

    h3 {
        transition: $transition;
        text-transform: capitalize;

        margin: {
            bottom: 15px;
            top: 23px;
        }

        ;

        font: {
            size: 20px;
            weight: 600;
        }

        ;
    }

    p {
        font-size: 15.5px;
        transition: $transition;
    }

    span {
        bottom: 0;
        color: $black_color;
        opacity: .02;
        font-size: 200px;
        line-height: 180px;
        position: absolute;
        right: 0;
        transition: $transition;
    }

    &::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        height: 0;
        width: 100%;
        transition: $transition;
        background: $template_color;
        z-index: -1;
    }

    &:hover {
        box-shadow: 0px 8px 16px 0px rgba(146, 184, 255, 0.2);
        color: $white_color;
        transform: translateY(-10px);

        i {
            color: $white_color;
        }

        p {
            color: $white_color;
            opacity: 0.9;
        }

        h3 {
            color: $white_color;
        }

        span {
            color: $white_color;
            opacity: 0.2;
        }

        &::before {
            height: 100%;
        }
    }
}

/*==============================
CTA Area CSS
==============================*/
.cta-area {
    position: relative;
    z-index: 1;

    background: {
        image: url(../../assets/img/ctr-bg.jpg);
        position: center center;
        size: cover;
        repeat: no-repeat;
    }

    ;

    &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
        width: 100%;
        height: 100%;
        background: $black_color;
        opacity: .60;
    }
}

.cta-content {
    text-align: center;

    .section-title {
        padding-left: 0;

        &::after,
        &::before {
            display: none;
        }

        h2 {
            color: $white_color;
            font-weight: 600;

            margin: {
                top: 25px;
                bottom: 25px;
            }

            ;
        }

        p {
            color: $white_color;
            opacity: .95;

            margin: {
                left: auto;
                right: auto;
                bottom: 30px;
            }

            ;
        }

        .btn {
            margin: {
                left: 8px;
                right: 8px;
            }

            ;

            &.btn-primary {

                &:hover,
                &:focus {
                    border-color: $white_color;
                }
            }
        }

        .view-work {
            background: transparent;
            border-color: $white_color;

            &:hover,
            &:focus {
                border-color: $template_color !important;
                background: $template_color;
                color: $white_color;
            }
        }
    }
}

/*==============================
Skill Area CSS
==============================*/
.skill-content {
    .section-title {
        margin-bottom: 40px;
    }

    .skills-list {
        .skill-item {
            margin-bottom: 25px;

            .skill-header {
                position: relative;
                margin-bottom: 10px;

                .skill-title {
                    margin-bottom: 0;

                    font: {
                        size: 16px;
                        weight: 600;
                    }

                    ;
                }

                .skill-percentage {
                    position: absolute;
                    right: 0;
                    top: -2px;
                    color: $black_color;

                    font: {
                        size: 15.5px;
                        weight: 500;
                    }

                    ;
                }
            }

            &:last-child {
                margin-bottom: 0;
            }

            .skill-bar {
                position: relative;
                width: 100%;

                .bar-inner {
                    position: relative;
                    width: 100%;
                    border-bottom: 7px solid $black_color;
                    border-radius: 30px;

                    .bar {
                        position: absolute;
                        left: 0px;
                        top: 0px;
                        width: 0px;
                        height: 7px;
                        background: $template_color;
                        transition: all 2000ms ease;
                        border-radius: 30px;
                    }
                }
            }
        }
    }
}

.skill-video {
    position: relative;

    .video-btn {
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        text-align: center;
        transform: translateY(-50%);

        a {
            position: relative;
            display: inline-block;
            width: 90px;
            height: 90px;
            line-height: 92px;
            background-color: $white_color;
            border-radius: 50%;
            color: $template_color;
            z-index: 1;
            font-size: 35px;

            &::after,
            &::before {
                content: '';
                display: block;
                position: absolute;
                top: 0;
                right: 0;
                z-index: -1;
                bottom: 0;
                left: 0;
                border-radius: 50%;
                border: 1px solid $white_color;
            }

            &::before {
                animation: ripple 2s linear infinite
            }

            &::after {
                animation: ripple 2s linear 1s infinite
            }

            &:hover,
            &:focus {
                background-color: $template_color;
                color: $white_color;
            }
        }
    }
}

@keyframes animate-positive {
    0% {
        width: 0;
    }
}

