/*==============================
Team Area CSS
==============================*/
.team-area {
    position: relative;
    z-index: 1;
}

.single-team-box {
    margin-bottom: 60px;
    text-align: center;
    position: relative;

    .image {
        img {
            width: 100%;
        }
    }

    .content {
        position: absolute;
        left: 0;
        bottom: -30px;
        right: 0;
        background: $white_color;
        margin-left: 15px;
        z-index: 1;
        transition: $transition;
        box-shadow: 0px 8px 16px 0px rgba(146, 184, 255, 0.2);
        padding: 20px;

        margin: {
            left: 15px;
            right: 15px;
        }

        ;

        .title {
            margin-bottom: 10px;
            transition: $transition;

            font: {
                size: 20px;
                weight: 600;
            }

            ;
        }

        .post {
            color: $template_color;
            transition: $transition;
            font-size: 15px;
        }
    }

    .social {
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(to top, rgba(0, 0, 0, 0.9) 20%, rgba(0, 0, 0, 0.3) 80%);
        transition: $transition;
        opacity: 0;
        visibility: hidden;

        ul {
            position: absolute;
            left: 0;
            right: 0;
            top: 50%;
            margin-top: -50px;
            transform: translateY(-35px);
            transition: $transition;

            li {
                display: inline-block;

                margin: {
                    left: 3px;
                    right: 3px;
                }

                ;

                a {
                    width: 35px;
                    height: 35px;
                    line-height: 35px;
                    color: $white_color;
                    display: block;
                    border: 1px solid $white_color;
                    border-radius: 50%;

                    &:hover {
                        border-color: $template_color;
                        background: $template_color;
                    }
                }
            }
        }
    }

    &:hover {
        .content {
            bottom: 25%;
            background: transparent;
            box-shadow: unset;

            .title {
                color: $white_color;
            }

            .post {
                color: $white_color;
            }
        }

        .social {
            opacity: 1;
            visibility: visible;

            ul {
                transform: translateY(0);
            }
        }
    }
}

.team-slides {
    &.owl-theme {
        .owl-nav {
            &.disabled+.owl-dots {
                margin-top: 20px;
            }
        }
    }
}

