/*==============================
Why We Are Different CSS
==============================*/
.why-we-different-tabset {
    &.tabset {
        input[type="radio"] {
            display: none;
        }

        .tab-panel {
            display: none;
        }

        input {
            &:first-child:checked~.tab-panels {
                .tab-panel {
                    &:first-child {
                        display: block;
                    }
                }
            }

            &:nth-child(3):checked~.tab-panels {
                .tab-panel {
                    &:nth-child(2) {
                        display: block;
                    }
                }
            }

            &:nth-child(5):checked~.tab-panels {
                .tab-panel {
                    &:nth-child(3) {
                        display: block;
                    }
                }
            }

            &:nth-child(7):checked~.tab-panels {
                .tab-panel {
                    &:nth-child(4) {
                        display: block;
                    }
                }
            }

            &:nth-child(9):checked~.tab-panels {
                .tab-panel {
                    &:nth-child(5) {
                        display: block;
                    }
                }
            }

            &:nth-child(11):checked~.tab-panels {
                .tab-panel {
                    &:nth-child(6) {
                        display: block;
                    }
                }
            }
        }

        label {
            position: relative;
            display: inline-block;
            padding: 15px 32px;
            cursor: pointer;
            background: transparent;
            transition: $transition;
            border: 1px solid #dddddd;
            border-radius: 5px;

            font: {
                size: 18px;
                weight: 500;
            }

            ;

            margin: {
                right: 10px;
                bottom: 0;
            }

            ;
        }

        input:checked+label {
            border-color: $template_color;
            background: $template_color;
            color: $white_color;
        }
    }

    .tab-panel {
        margin-top: 35px;
    }
}

.why-we-different-content {
    padding-left: 10px;

    h3 {
        margin-bottom: 15px;

        font: {
            size: 25px;
            weight: 500;
        }

        ;
    }

    ul {
        margin: {
            top: 20px;
            bottom: 0;
        }

        ;

        li {
            margin-bottom: 10px;
            font-weight: 300;
            color: #727695;

            &:last-child {
                margin-bottom: 0;
            }

            i {
                color: $template_color;
                margin-right: 5px;
            }
        }
    }
}

.why-we-different-img {
    text-align: center;
}

/*==============================
Work Area CSS
==============================*/
.work-area {
    position: relative;
    z-index: 1;
}

.shorting-menu {
    display: inline-block;
    box-shadow: 0 0 30px 0 rgba(67, 67, 67, 0.15);
    padding: 20px;
    border-radius: 50px;

    .filter {
        border: none;
        background: #f4f4f4;
        padding: 9px 30px;
        text-transform: capitalize;
        cursor: pointer;
        transition: $transition;
        border-radius: 30px;
        color: $black_color;
        margin-right: 10px;

        font: {
            size: 16px;
            weight: 500;
        }

        ;

        &.active,
        &:hover {
            background: $template_color;
            color: $white_color;
        }

        &:last-child {
            margin-right: 0;
        }
    }
}

.shorting {
    margin-top: 40px;

    .mix {
        display: none;
    }
}

.single-work {
    margin-bottom: 30px;
    position: relative;
    text-align: center;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        background: $black_color;
        left: 0;
        opacity: 0;
        right: 0;
        bottom: 0;
        visibility: hidden;
        transition: $transition;
    }

    .work-content {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        background: $white_color;
        opacity: 0;
        visibility: hidden;
        transition: $transition;

        margin: {
            left: 20px;
            right: 20px;
        }

        ;

        padding: {
            top: 20px;
            bottom: 20px;
            left: 10px;
            right: 10px;
        }

        ;

        h3 {
            margin-bottom: 10px;

            font: {
                weight: 600;
                size: 20px;
            }

            ;
        }

        ul {
            li {
                display: inline-block;
                font-size: 15px;

                margin: {
                    left: 1px;
                    right: 1px;
                }

                ;
            }
        }
    }

    .popup-btn {
        position: absolute;
        top: 20px;
        right: 40px;
        width: 45px;
        text-align: center;
        height: 45px;
        line-height: 45px;
        border-radius: 50%;
        color: $white_color;
        font-size: 22px;
        opacity: 0;
        visibility: hidden;
        border: 1px solid $white_color;
        transition: $transition;

        &:hover {
            background: $template_color;
            border-color: $template_color;
        }
    }

    &:hover {
        &::before {
            margin: 10px;
            opacity: .50;
