/*==============================
FAQ Area CSS
==============================*/
.faq-image {
    width: 100%;
    height: 100%;

    background: {
        image: url(../../assets/img/faq-img.jpg);
        size: cover;
        position: center center;
        repeat: no-repeat;
    }

    ;

    img {
        display: none;
    }
}

.faq-accordion {
    .card {
        border-radius: 5px !important;
        display: block;
        margin-bottom: 15px;
        border: none;
        background-color: $white_color;
        box-shadow: 2px 8px 20px 0 rgba(25, 42, 70, 0.13) !important;

        .card-header {
            padding: 0;
            margin-bottom: 0;
            border: none;
            background: transparent;

            &:first-child {
                border-radius: 0;
            }

            a {
                display: block;

                &:hover {
                    background: $template_color;
                }

                h5 {
                    overflow: hidden;
                    padding: 20px 40px 20px 20px;
                    color: $white_color;
                    background: $template_color;
                    position: relative;

                    font: {
                        size: 16px;
                        weight: 500;
                    }

                    ;
                }

                i {
                    position: absolute;
                    right: 20px;
                    top: 50%;
                    font-size: 16px;
                    transform: translateY(-50%) rotate(180deg);
                }

                &.collapsed {
                    h5 {
                        color: $black_color;
                        background: transparent;
                        transition: $transition;

                        &:hover {
                            color: $white_color;
                        }
                    }

                    i {
                        transform: translateY(-50%) rotate(0);
                    }
                }
            }
        }

        .card-body {
            line-height: 1.9;
            background: transparent;
            color: #727695;
        }
    }
}

