            margin: {
                bottom: 6px;
                top: 20px;
            }

            ;

            font: {
                size: 20px;
                weight: 500;
            }

            ;
        }

        span {
            color: $template_color;
            display: block;

            font: {
                weight: 500;
                size: 15px;
            }

            ;
        }

        &::before {
            content: '';
            position: absolute;
            right: 290px;
            bottom: 45px;
            width: 110px;
            height: 75px;

            background: {
                position: center center;
                size: cover;
                repeat: no-repeat;
                image: url(../../assets/img/arrow.png);
            }

            ;
        }
    }
}

.feedback-slides {
    &.owl-theme {
        margin-bottom: -40px;

        .single-feedback-item {
            margin-bottom: 40px;
        }

        .owl-nav {
            margin-top: 0;

            [class*=owl-] {
                color: #87a1c1;
                font-size: 25px;
                margin: 0;
                padding: 0;
                background: transparent;
                width: 48px;
                height: 48px;
                line-height: 48px;
                position: absolute;
                left: 50px;
                transition: $transition;
                top: 50%;
                transform: translateY(-50%);
                opacity: 0;
                visibility: hidden;

                border: {
                    radius: 50%;
                    style: solid;
                    color: #e3e4ef;
                    width: 2px;
                }

                ;

                &.owl-next {
                    left: auto;
                    right: 50px;
                }

                &:hover,
                &:focus {
                    background-color: $template_color;
                    color: $white_color;
                    border-color: $template_color;
                    box-shadow: 0px 8px 16px 0px rgba(241, 96, 1, 0.2);
                }

                &.disabled {
                    color: #e3e4ef;

                    &:hover,
                    &:focus {
                        background-color: transparent;
                        color: #e3e4ef;
                        border-color: #e3e4ef;
                        box-shadow: unset;
                    }
                }
            }
        }

        &:hover,
        &:focus {
            .owl-nav {
                [class*=owl-] {
                    left: 70px;
                    opacity: 1;
                    visibility: visible;

                    &.owl-next {
                        left: auto;
                        right: 70px;
                    }
                }
            }
        }
    }
}

/*==============================
Blog Area CSS
==============================*/
.blog-area {
    position: relative;
    z-index: 1;
}

.single-blog-item {
    position: relative;
    padding: 30px;
    border: 1px solid #eeeeee;
    margin-bottom: 30px;
    z-index: 1;
    overflow: hidden;

    background: {
        size: cover;
        position: center center;
        repeat: no-repeat;
    }

    ;

    &::before {
        z-index: -1;
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: $white_color;
        transition: $transition;
    }

    span {
        display: block;
        color: $template_color;
    }

    span,
    p,
    h3 {
        position: relative;
        transition: $transition;
    }

    p {
        font-size: 15.5px;
    }

    h3 {
        line-height: 1.5;

        margin: {
            top: 12px;
            bottom: 12px;
        }

        ;

        font: {
            size: 20px;
            weight: 500;
        }

        ;

        a {
            display: inline-block;

            &:hover {
                color: $template_color !important;
            }
        }
    }

    .link-btn {
        position: absolute;
        right: 0;
        bottom: 0;
        width: 45px;
        height: 40px;
        line-height: 44px;
        text-align: center;
        box-shadow: 0px 0px 25px 0px rgba(0, 27, 103, 0.3);
        border-radius: 50% 0 0 0;
    }

    &.bg1 {
        background-image: url(../../assets/img/blog-img1.jpg);
    }

    &.bg2 {
        background-image: url(../../assets/img/blog-img2.jpg);
    }

    &.bg3 {
        background-image: url(../../assets/img/blog-img3.jpg);
    }

    &.bg4 {
        background-image: url(../../assets/img/blog-img4.jpg);
    }

    &.bg5 {
        background-image: url(../../assets/img/blog-img5.jpg);
