/*==============================
Go Top CSS
==============================*/
.go-top {
    position: fixed;
    cursor: pointer;
    bottom: -42px;
    right: 15px;
    color: $white_color;
    background-color: $template_color;
    z-index: 4;
    width: 42px;
    text-align: center;
    height: 42px;
    line-height: 42px;
    opacity: 0;
    visibility: hidden;
    font-size: 20px;
    transition: $transition;
    border-radius: 5px 5px 0 0;

    i {
        position: absolute;
        left: 0;
        right: 0;
        top: 55%;
        transform: translateY(-55%);
        transition: .3s;
    }

    &.active {
        bottom: 0;
        opacity: 1;
        visibility: visible;
    }

    &:hover {
        i {
            margin-top: -3px;
        }
    }
}
